.root {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: var(--color1);
}


.label {
  font-family: "Ranchers", cursive;
  display: block;
  display: flex;
  font-size: 26px;
  flex-direction: column;
  align-items: center;
}

.input {
  display: block;
  padding: 5px;
  font-family: "Ranchers", cursive;
  font-size: 20px;
  margin: 10px;
  color: var(--color4);
  border: none;

}
